import { Component,Pipe, ViewChild, OnInit, ElementRef, PipeTransform } from "@angular/core";
import { ToastController, Platform, ModalController } from "@ionic/angular";
import { ChooseCarPage } from "./modal/choose-car";

@Component({
  selector: "page-carCompare",
  templateUrl: "carCompare.html",
  styleUrls: ["./carCompare.scss"],
})
export class CarComparePage implements OnInit {
 
public firstCarData ={
  id:1,
  brand:'',
  car_name:'',
  variant_name:'',
  carData:{},
  varientData:{}
}

public secondCarData ={
  id:2,
  brand:'',
  car_name:'',
  variant_name:'',
  carData:{},
  varientData:{}
}

  constructor(
    public toastController: ToastController,
    public modalController: ModalController,
  ) {}

  ngOnInit(){

  }

  async openChooseCarModal(id) {
    const modal = await this.modalController.create({
      component: ChooseCarPage
    });

    modal.onDidDismiss().then((dataReturned) => {
      if (dataReturned.data) {
          console.log(dataReturned)
          let CarObj = dataReturned;

          if(id == 1){
            this.firstCarData ={
              id:1,
              brand:CarObj.data.brand,
              car_name:CarObj.data.productName,
              variant_name:CarObj.data.selectedVarients[0]['variant_name'],
              carData:CarObj.data.car_data,
              varientData:CarObj.data.selectedVarients[0]
            }
          }


          if(id == 2){
            this.secondCarData ={
              id:2,
              brand:CarObj.data.brand,
              car_name:CarObj.data.productName,
              variant_name:CarObj.data.selectedVarients[0]['variant_name'],
              carData:CarObj.data.car_data,
              varientData:CarObj.data.selectedVarients[0]
            }
          }
      }
    });

    return await modal.present();
  }


}


 